define('ember-views/mixins/class_names_support', ['exports', 'ember-metal/core', 'ember-metal/mixin', 'ember-runtime/system/native_array', 'ember-metal/utils'], function (exports, Ember, mixin, native_array, utils) {

  'use strict';

  /**
  @module ember
  @submodule ember-views
  */
  var EMPTY_ARRAY = [];

  /**
    @class ClassNamesSupport
    @namespace Ember
  */
  var ClassNamesSupport = mixin.Mixin.create({
    concatenatedProperties: ["classNames", "classNameBindings"],

    init: function () {
      this._super.apply(this, arguments);

            this.classNameBindings = native_array.A(this.classNameBindings.slice());

            this.classNames = native_array.A(this.classNames.slice());
    },

    /**
      Standard CSS class names to apply to the view's outer element. This
      property automatically inherits any class names defined by the view's
      superclasses as well.
       @property classNames
      @type Array
      @default ['ember-view']
    */
    classNames: ["ember-view"],

    /**
      A list of properties of the view to apply as class names. If the property
      is a string value, the value of that string will be applied as a class
      name.
       ```javascript
      // Applies the 'high' class to the view element
      Ember.View.extend({
        classNameBindings: ['priority']
        priority: 'high'
      });
      ```
       If the value of the property is a Boolean, the name of that property is
      added as a dasherized class name.
       ```javascript
      // Applies the 'is-urgent' class to the view element
      Ember.View.extend({
        classNameBindings: ['isUrgent']
        isUrgent: true
      });
      ```
       If you would prefer to use a custom value instead of the dasherized
      property name, you can pass a binding like this:
       ```javascript
      // Applies the 'urgent' class to the view element
      Ember.View.extend({
        classNameBindings: ['isUrgent:urgent']
        isUrgent: true
      });
      ```
       This list of properties is inherited from the view's superclasses as well.
       @property classNameBindings
      @type Array
      @default []
    */
    classNameBindings: EMPTY_ARRAY
  });

  exports['default'] = ClassNamesSupport;

});