define('ember-htmlbars/hooks/bind-self', ['exports', 'ember-metal/streams/simple-stream', 'ember-htmlbars/utils/subscribe'], function (exports, SimpleStream, subscribe) {

  'use strict';



  exports['default'] = bindSelf;

  /**
  @module ember
  @submodule ember-htmlbars
  */

  function bindSelf(env, scope, self) {
    if (self && self.isView) {
      scope.view = self;
      newStream(scope.locals, "view", self, null);
      newStream(scope.locals, "controller", scope.locals.view.getKey("controller"));
      newStream(scope, "self", scope.locals.view.getKey("context"), null, true);
      return;
    }

    newStream(scope, "self", self, null);
  }

  function newStream(scope, key, newValue, renderNode, isSelf) {
    var stream = new SimpleStream['default'](newValue, isSelf ? null : key);
    if (renderNode) {
      subscribe['default'](renderNode, scope, stream);
    }
    scope[key] = stream;
  }

});