define('ember-htmlbars/hooks/bind-local', ['exports', 'ember-metal/streams/stream', 'ember-metal/streams/simple-stream'], function (exports, Stream, SimpleStream) {

  'use strict';



  exports['default'] = bindLocal;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function bindLocal(env, scope, key, value) {
    var isExisting = scope.locals.hasOwnProperty(key);

    if (isExisting) {
      var existing = scope.locals[key];

      if (existing !== value) {
        existing.setSource(value);
      }

      existing.notify();
    } else {
      var newValue = Stream['default'].wrap(value, SimpleStream['default'], key);
      scope.locals[key] = newValue;
    }
  }

});