define('ember-htmlbars/helpers', ['exports', 'ember-metal/platform/create', 'ember-htmlbars/keywords'], function (exports, o_create, keywords) {

  'use strict';

  exports.registerHelper = registerHelper;

  /**
  @module ember
  @submodule ember-htmlbars
  */

  /**
    @private
    @method _registerHelper
    @for Ember.HTMLBars
    @param {String} name
    @param {Object|Function} helperFunc the helper function to add
  */
  var helpers = o_create['default'](null);
  function registerHelper(name, helperFunc) {
    if (helperFunc.isLegacyViewHelper) {
      keywords.registerKeyword(name, function (morph, env, scope, params, hash, template, inverse, visitor) {
        env.hooks.keyword("view", morph, env, scope, [helperFunc.viewClass], hash, template, inverse, visitor);
        return true;
      });
      return;
    }

    helpers[name] = helperFunc;
  }

  exports['default'] = helpers;

});