define('ember-routing/initializers/routing-service', ['ember-runtime/system/lazy_load', 'ember-routing/services/routing'], function (lazy_load, RoutingService) {

  'use strict';

  lazy_load.onLoad("Ember.Application", function (Application) {
    Application.initializer({
      name: "routing-service",
      initialize: function (registry) {
        // Register the routing service...
        registry.register("service:-routing", RoutingService['default']);
        // Then inject the app router into it
        registry.injection("service:-routing", "router", "router:main");
      }
    });
  });

});