define('ember-routing-views/initializers/link-to-component', ['ember-runtime/system/lazy_load', 'ember-routing-views/views/link'], function (lazy_load, linkToComponent) {

  'use strict';

  lazy_load.onLoad("Ember.Application", function (Application) {
    Application.initializer({
      name: "link-to-component",
      initialize: function (registry) {
        registry.register("component:-link-to", linkToComponent['default']);
      }
    });
  });

});