define('ember-htmlbars/hooks/subexpr', ['exports', 'ember-htmlbars/system/lookup-helper', 'ember-metal/merge', 'ember-metal/streams/stream', 'ember-metal/platform/create', 'ember-metal/streams/utils'], function (exports, lookupHelper, merge, Stream, create, utils) {

  'use strict';



  exports['default'] = subexpr;

  /**
  @module ember
  @submodule ember-htmlbars
  */

  function subexpr(env, scope, helperName, params, hash) {
    // TODO: Keywords and helper invocation should be integrated into
    // the subexpr hook upstream in HTMLBars.
    var keyword = env.hooks.keywords[helperName];
    if (keyword) {
      return keyword(null, env, scope, params, hash, null, null);
    }

    var helper = lookupHelper['default'](helperName, scope.self, env);
    var invoker = function (params, hash) {
      return env.hooks.invokeHelper(null, env, scope, null, params, hash, helper, { template: {}, inverse: {} }, undefined).value;
    };

    //Ember.assert("A helper named '"+helperName+"' could not be found", typeof helper === 'function');

    var label = labelForSubexpr(params, hash, helperName);
    return new SubexprStream(params, hash, invoker, label);
  }

  function SubexprStream(params, hash, helper, label) {
    this.init(label);
    this.source = { params: params, hash: hash };
    this.helper = helper;
    this.subscribed = false;
  }

  function labelForSubexpr(params, hash, helperName) {
    return function () {
      var paramsLabels = labelsForParams(params);
      var hashLabels = labelsForHash(hash);
      var label = "(" + helperName;
      if (paramsLabels) {
        label += " " + paramsLabels;
      }
      if (hashLabels) {
        label += " " + hashLabels;
      }
      return "" + label + ")";
    };
  }

  function labelsForParams(params) {
    return utils.labelsFor(params).join(" ");
  }

  function labelsForHash(hash) {
    var out = [];

    for (var prop in hash) {
      out.push("" + prop + "=" + utils.labelFor(hash[prop]));
    }

    return out.join(" ");
  }

  SubexprStream.prototype = create['default'](Stream['default'].prototype);

  merge['default'](SubexprStream.prototype, {
    compute: function () {
      var sourceParams = this.source.params;
      var sourceHash = this.source.hash;

      var params = new Array(sourceParams.length);
      var hash = {};

      for (var i = 0, l = sourceParams.length; i < l; i++) {
        params[i] = utils.read(sourceParams[i]);
      }

      for (var prop in sourceHash) {
        hash[prop] = utils.read(sourceHash[prop]);
      }

      return this.helper(params, hash);
    },

    _super$subscribe: Stream['default'].prototype.subscribe,

    subscribe: function () {
      if (!this.subscribed) {
        var sourceParams = this.source.params;
        var sourceHash = this.source.hash;

        for (var i = 0, l = sourceParams.length; i < l; i++) {
          utils.addDependency(this, utils.subscribe(sourceParams[i], this._didChange, this));
        }

        for (var prop in sourceHash) {
          utils.addDependency(utils.subscribe(sourceHash[prop], this._didChange, this));
        }

        this.subscribed = true;
      }

      return this._super$subscribe.apply(this, arguments);
    },

    _didChange: function () {
      this.notify();
    },

    _super$destroy: Stream['default'].prototype.destroy,

    destroy: function (prune) {
      if (this._super$destroy(prune)) {
        this.source = undefined;
        return true;
      }
    }
  });

});